body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#app-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}

#main-header {
  padding: 40px 0 30px;
  text-align: center;
}

#search {
  margin: 0 auto 30px;
}
#search input {
  width: 60%;
  height: 2.4em;
  padding-left: 10px;
  border-color: black;
}
#results-table { 
  margin: 0 auto; 
  box-shadow: 0px 2px 6px rgba(0,0,0,0.5);
}

#no-matches {
  font-size: 2.4em;
  text-align: center;
}
main {
  -webkit-flex: 1 1;
          flex: 1 1;
}
footer {
  padding-top: 20px;
  padding-bottom: 40px;
}

footer p {
  margin-bottom: 0;
  text-align: center;
}
